@import '../../../Variables.scss';
.ProjectIntro {
  position: relative;
  width: 100%;
  border-bottom: 1px dashed black;
  padding-bottom: 30px;
}

.introCard {
  display: flex;
  align-items: stretch;
  .logo {
    width: 200px;
  }
}

.textWrap {
  text-align: left;
  margin-left: 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .nameWrap{
    display: flex;
    justify-content: space-between;
    align-items: center;

    .editButton{
      display: flex;
      justify-content: flex-end;
      align-items: center;
      font-size: 18px;
      font-weight: 600;
      color: $color-primary;
      line-height: 20px;
      img{
        width: 30px;
        margin-right: 10px;
        margin-top: -3px;
      }
    }
  }
  h2 {
    font-weight: 900;
    font-size: 36px;
    line-height: 60px;
    margin: 0;
  }
  p {
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    margin: 0;
  }
  .linkWrap {
    margin-top: auto;
    font-weight: 800;
    font-size: 13px;
    line-height: 22px;
    color: $color-primary;
    a {
      margin-right: 25px;
      white-space: nowrap;
      img {
        width: 20px;
        margin-right: 5px;
      }
    }
  }
}


@media (max-width: 765px) {
  .introCard {
    flex-direction: column;

    .logo {
      width: 150px;
    }
  }

  .textWrap {
    margin-left: 0;
    h2 {
      font-size: 25px;
      line-height: 30px;
      margin: 20px 0;
    }
    p {
      font-weight: 400;
      font-size: 15px;
      line-height: 20px;
      text-align: justify;
      margin: 0;
    }
    .linkWrap {
      margin-top: 10px;
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
      a {
        width: calc(50% - 5px);
        margin-right: 0;
        white-space: nowrap;
        font-size: 12px;
        img {
          width: 18px;
          margin-left:5px;
        }
      }
    }
  }

  .editButton{
    position: absolute;
    right: 20px;
    top: 20px;
    font-size: 16px;
  }
}
