@import '../../../Variables.scss';
.DetailPage {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  .grayBox {
    padding: 20px;
    background-color: $color-dark-gray;
    border-radius: 4px;
    text-align: left;
  }
  h2 {
    text-align: left;
    font-weight: 900;
    font-size: 18px;
    line-height: 25px;
    color: $color-primary;
    text-transform: uppercase;
    margin: 0;
  }
  .infoItem {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .title {
      font-weight: bold;
      font-size: 14px;
      line-height: 25px;
      color: $color-text-gray;
      text-transform: uppercase;
    }
    .valueWrap {
      color: black;
      display: flex;
      max-width: calc(100% - 100px);
      align-items: center;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      img {
        width: 15px;
        margin: 0 5px;
      }
      .mainValue {
        font-weight: 900;
        font-size: 18px;
        line-height: 25px;
      }
      .secondaryValue {
        display: block;
        font-weight: bold;
        font-size: 14px;
        line-height: 25px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
    }
  }
  .divider {
    opacity: 0.1;
    border-right: 2px solid #000000;
  }
  .statsWrap {
    position: relative;
    width: 47%;

    .tokenWrap {
      margin-top: 20px;
      h2{
        text-transform: initial;
      }
      .balanceWrap {
        margin-top: 20px;
        .balanceWrapHeader{
          display: flex;
          align-items: center;
          justify-content: space-between;

          .buttonGroup{
            display: flex;
            column-gap: 10px;
            .button{
              width: 60px;
            }
          }
        }
        h2 {
          font-weight: bold;
          font-size: 14px;
          line-height: 25px;
          color: $color-text-gray;
          text-transform: uppercase;
        }
        span {
          display: flex;
          align-items: center;
          h2 {
            font-weight: bold;
            font-size: 18px;
            line-height: 25px;
            color: #000000;
            margin: 0;
          }
          p {
            font-weight: 400;
            font-size: 14px;
            line-height: 25px;
            color: #000000;
            text-transform: uppercase;
            margin: 0 5px;
          }
        }
        hr {
          border-style: dashed;
          border-color: #000000;
          opacity: 0.2;
          margin: 20px 0;
        }
        .claimable {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: space-between;
          h2 {
            margin: 0;
            font-weight: bold;
            font-size: 14px;
            line-height: 25px;
            color: $color-text-gray;
          }
          p {
            font-weight: bold;
            font-size: 14px;
            line-height: 25px;
            color: #000000;
          }
        }
      }
    }
  }
  .actionWrap {
    width: 47%;

    .fundingCycleHeaderWrap{
      display: flex;
      justify-content: space-between;
      align-items: center;

      .statsTag{
        color: $color-red;
        font-weight: bold;
        font-size: 16px;
      }
      .countdown{
        justify-content: flex-end;
        color: $color-primary;
      }
    }
    .fundWrap {
      margin-top: 10px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .statsWrap {
        display: flex;
        width: 100%;
        .infoItem {
          .title {
            font-size: 14px;
            text-transform: initial;
          }
        }
      }
      .distributionButton{
        margin-left: 20px;
        width: auto;
        flex-grow: 1;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        max-width: 300px;
        padding: 10px 30px;
      }
    }
  }
}

@media(max-width: 765px){
  .DetailPage {
    flex-direction: column;
    h2 {
      text-align: left;
      font-weight: 900;
      font-size: 18px;
      line-height: 25px;
      color: $color-primary;
      text-transform: uppercase;
      margin: 0;
    }
    .infoItem {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .title {
        font-weight: bold;
        font-size: 13px;
        line-height: 25px;
        color: $color-text-gray;
      }
      .valueWrap {
        color: black;
        display: flex;
        align-items: center;
        &.address{
          display: block;
        }
        img {
          width: 15px;
          margin: 0 5px;
        }
        .mainValue {
          font-weight: 700;
          font-size: 15px;
          line-height: 25px;
        }
        .secondaryValue {
          font-weight: bold;
          font-size: 14px;
          line-height: 25px;
        }
      }
    }
    .divider {
      opacity: 0.1;
      border-right: none;
    }
    .statsWrap {
      position: relative;
      width: 100%;

      .tokenWrap {
        .balanceWrap {
          margin-top: 10px;
          hr {
            margin: 15px 0;
          }
          .claimable {
            flex-direction: column;
            align-items: flex-start;
            h2 {
              margin: 0;
              font-weight: bold;
              font-size: 14px;
              line-height: 25px;
              color: $color-text-gray;
            }
            p {
              font-weight: bold;
              font-size: 14px;
              line-height: 25px;
              color: #000000;
            }
          }
        }
      }
    }
    .actionWrap {
      margin-top: 20px;
      padding-top: 10px;
      width: 100%;
      border-top:2px solid #000000;
      .fundWrap {
        margin-top: 10px;
        flex-direction: column;
        .statsWrap {
          margin-bottom: 10px;
        }
        .distributionButton{
          margin-left: 0;
        }
      }
    }
  }
}

