@import '../../../Variables.scss';
.AuthMenu {
  position: relative;
  .authMenuWrap {
    display: flex;
    align-items: center;
  }
  .preLogin {
  }
  .postLogin {
    cursor: pointer;
    img {
      width: 35px;
      margin-right: 5px;
    }
  }
}

.button {
  width: 100px;
  margin: 0 5px;
}
.textButton {
  font-size: 14px;
  font-weight: 700;
  color: $color-primary;
  text-align: center;
  max-width: 15ch;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  user-select: none;
  cursor: pointer;
}
.walletModalWrapper {
  :global(.ant-modal-content) {
    box-sizing: border-box;
    background: white;
    border: 1px;
    border-radius: 10px;
    padding: 0;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    font-family: 'Battambang', sans-serif;
  }
  :global(.ant-modal-body) {
    padding: 15px 20px;
  }
}
@media (max-width: 765px) {

  .AuthMenu {
    .authMenuWrap{
      flex-direction: column;
    }
    .postLogin {
      img{
        width: 25px;
      }
    }
  }

  .button {
    height: 30px;
  }

}
