@import '../../Variables.scss';

.Footer {
  background-color: #121212;
  display: flex;
  justify-content: flex-end;

  .mainContainer {
    padding: 20px 0;
    // border-top: 2px solid #eaeaea;
    display: flex;
    justify-content: space-between;
    align-items: center;
    > p {
      font-weight: 700;
      font-size: 14px;
      line-height: 20px;
      color: #bababa;
      margin-bottom: 0;
    }

    .mediaAndLangs {
      display: flex;
      justify-content: space-between;
      .langButton {
        // height: 40px;
        border: 1px solid #989898;
        box-sizing: border-box;
        filter: drop-shadow(0px 4px 10px rgba(63, 154, 85, 0.5));
        border-radius: 4px;
        // line-height: 40px;
        width: fit-content;
        text-align: center;
        padding: 0 10px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        color: #989898;
        font-weight: bold;
        margin-left: 20px;
        cursor: pointer;
        user-select: none;
      }
    }
  }
}
@media (max-width: 765px) {
  .mainContainer {
    flex-direction: column-reverse;
    row-gap: 10px;
  }
}
