@import '../../../Variables.scss';
.ProjectAction {
  position: relative;
  width: 100%;
  background-color: transparent;
  //font-family: 'Sarpanch';
}

.donateSuccessModal {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    width: 100%;
    max-width: 200px;
  }
  h1 {
    text-align: center;
    font-family: 'Battambang';
    font-style: normal;
    font-weight: 900;
    font-size: 20px;
    line-height: 60px;
    /* identical to box height, or 167% */

    color: #000000;

    text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.75);

    margin-bottom: 0;
  }
}

.headerTitle{
  font-weight: bold;
  font-size: 24px;
  line-height: 60px;
  text-align: left;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.75);
  margin-bottom: 0;
}

.headerWrap {
  position: relative;
  width: 100%;
  background: $color-gray-background;

  border: 1px solid $color-white-border;
  box-sizing: border-box;
  border-radius: 6px;
  padding: 20px;
  margin-bottom: 30px;

  // .titleTabWrap {

  // .tabWrap {
  //   display: flex;
  //   text-align: center;
  //   :first-child {
  //     border-radius: 6px 0px 0px 0px;
  //   }
  //   :last-child {
  //     border-radius: 0px 6px 0px 0px;
  //   }
  //   .tab {
  //     width: 200px;
  //     height: 50px;
  //     outline: 1px solid $color-border-gray;
  //     box-sizing: border-box;
  //     font-size: 13px;
  //     line-height: 50px;
  //     background-color: $color-border-gray;
  //     opacity: 0.8;
  //     user-select: none;
  //     cursor: pointer;

  //     &.active {
  //       background-color: white;
  //       opacity: 1;
  //     }
  //   }
  // }
  // }
  .inputWrap {
    display: flex;
    // flex-direction: column;
    // align-items: flex-start;
    justify-content: space-between;
    // width: 45%;
    .inputGroup {
      width: 47%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      :global(.ant-input-affix-wrapper) {
        border-color: $color-primary;
        height: 40px;
        width: auto;
        flex-grow: 1;
      }
      :global(.ant-input-affix-wrapper-focused) {
        box-shadow: 0 0 0 2px rgba(63, 154, 85, 0.2) !important;
      }
      .button {
        height: 40px;
        margin-left: 10px;
        // border-radius: 2px;
        &.inactive {
          cursor: not-allowed;
          opacity: 0.7;
        }
      }

      .inputSuffix {
        font-weight: bold;
        font-size: 14px;
        color: $color-primary;
        .rightArrow {
          color: $color-primary;
          margin-left: 3px;
        }
      }
    }
    .exchangeGroup {
      width: 49%;
      display: flex;
      justify-content: space-between;
      padding-left: 1.1rem;
      .endButton {
        background-color: $color-red;
        text-transform: capitalize;
        align-self: center;
      }
      .exchangeInfoWrapper {
        .exchangeInfo {
          display: flex;
          align-items: center;
          font-weight: bold;
          font-size: 12px;
          line-height: 25px;
          :first-child {
            margin-right: 10px;
          }
          p {
            margin: 0;
          }
          .title {
            color: $color-text-gray;
          }
          .value {
            text-transform: uppercase;
            a {
              color: $color-primary;
              text-transform: uppercase;
              text-decoration: underline;
            }
          }
        }
      }
    }
  }
}

.mintInfo {
  text-align: center;
  .mintTab {
    width: 200px;
    height: 50px;
    outline: 1px solid $color-border-gray;
    box-sizing: border-box;
    font-size: 13px;
    line-height: 50px;
    background-color: $color-border-gray;
    opacity: 0.8;
    user-select: none;
    cursor: pointer;
    border-radius: 6px 6px 0px 0px;

    &.active {
      background-color: white;
      opacity: 1;
    }
  }
  .mintDetail {
    background-color: white;
    //border: 1px solid #dadee8;
    box-sizing: border-box;
    border-radius: 0 6px 6px 6px;
    padding: 30px;

    .inputGroup {
      display: flex;
      justify-content: space-between;
      gap: 20px;
    }

    .buttonGroup {
      margin-top: 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 20px;
      .mintButton {
        //align-self: flex-end;
      }
      > p {
        font-size: 20px;
        line-height: 22px;
        margin-bottom: 8px;
        color: #f10d0d;
        font-weight: bold;
        text-align: right;
        //align-self: flex-end;
        cursor: pointer;
      }
    }
  }
}

.mainWrap {
  background-color: white;
  outline: 1px solid $color-border-gray;
  box-sizing: border-box;
  border-radius: 0px 6px 6px 6px;
  padding: 30px;
  margin-top: 0.5rem;
}

@media (max-width: 765px) {
  .headerWrap {
    flex-direction: column-reverse;
    h2 {
      font-weight: bold;
      font-size: 24px;
      line-height: 60px;
      text-align: left;
    }
    // .titleTabWrap {

    // .tabWrap {
    //   display: flex;
    //   :first-child {
    //     border-radius: 6px 0px 0px 0px;
    //   }
    //   :last-child {
    //     border-radius: 0px 6px 0px 0px;
    //   }
    //   .tab {
    //     width: 200px;
    //     height: 50px;
    //     outline: 1px solid $color-border-gray;
    //     box-sizing: border-box;
    //     font-size: 13px;
    //     line-height: 50px;
    //     background-color: $color-border-gray;
    //     opacity: 0.8;
    //     user-select: none;
    //     cursor: pointer;

    //     &.active {
    //       background-color: white;
    //       opacity: 1;
    //     }
    //   }
    // }
    // }

    .inputWrap {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 100%;

      .inputGroup {
        margin-bottom: 10px;
        width: 100%;
      }
      .exchangeGroup {
        padding: 0;
        width: 100%;
      }
    }
  }

  .mainWrap {
    padding: 15px;
  }

  .mintInfo {
    flex-wrap: wrap;
    .mintTab {
      width: 150px;
      height: auto;
      line-height: 40px;
    }
    .mintDetail {
      flex-direction: column;
      row-gap: 20px;
      .inputGroup {
        flex-direction: column;
      }
      .buttonGroup {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 20px;
        div {
          flex: 1;
          padding: 10px 20px;
        }
        p {
          flex: 1;
          text-align: center;
        }
      }
    }
  }
}
