@import '../../../Variables.scss';
.AuthButton {
  position: relative;
  padding: 0 10px;
  .authMenuWrap {
    display: flex;
    align-items: center;
  }
  .preLogin {
  }
  .postLogin {
    cursor: pointer;
    img {
      width: 35px;
      margin-right: 5px;
    }
  }
}

.button {
  width: 100px;
  margin: 0 10px;
}
.textButton {
  margin-left: 10px;
  font-size: 14px;
  font-weight: 700;
  color: $color-primary;
  text-align: center;
  // max-width: 15ch;
  // overflow: hidden;
  // text-overflow: ellipsis;
  white-space: nowrap;
  user-select: none;
  cursor: pointer;
}
@media (max-width: 765px) {
  .AuthButton {
    .authMenuWrap {
      // flex-direction: column;
      // height: 30px;
    }
    .preLogin {
      display: flex;
      // flex-direction: column;
      row-gap: 10px;
    }
    .postLogin {
      flex-direction: row;
      img {
        width: 25px;
      }
    }
  }

  .button {
    height: 30px;
  }
}
