@import '../../../../Variables.scss';
.inputWrapper {
  width: 100%;
  > p {
    text-align: left;
    margin: 0;
    padding: 0;
  }
  > input {
    border: 1px solid $color-primary;
    height: 40px;
    width: 100%;
    box-sizing: border-box;
    border-radius: 2px;
    padding-left: 11px;
  }
}
input:focus {
  box-shadow: 0 0 0 2px rgba(63, 154, 85, 0.2);
  outline: unset;
  // border: 1px solid $color-primary;
}
@media (max-width: 765px) {
  .inputWrapper {
    width: 100%;
  }
}
