@import '../../../Variables.scss';
.AccountInfoWrapper {
  width: 100%;
  border-bottom: 2px dashed rgba($color: #476a30, $alpha: 0.2);
  padding-bottom: 30px;

  .accountHeader {
    > h3 {
      font-weight: 700;
      font-size: 24px;
      line-height: 24px;
      color: #12162d;
    }
    display: flex;
    justify-content: space-between;
    .logOutHelper {
      font-weight: 700;
      font-size: 16px;
      line-height: 25px;
      color: #3f9a55;
      gap: 10px;
      cursor: pointer;
      > img {
        display: inline-block;
        margin-right: 10px;
      }
    }
  }
  .accountDetail {
    display: flex;
    align-items: center;
    justify-content: center;
    // justify-content: space-evenly;
    .account {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      > img {
        width: 120px;
        margin-bottom: 20px;
      }
      > p {
        font-weight: 700;
        font-size: 20px;
        line-height: 25px;
        color: #000000;
        overflow: hidden;
        // max-width: 25ch;
        text-overflow: ellipsis;
        white-space: nowrap;
        // margin-top: 10px;
      }
      .link {
        font-weight: 700;
        font-size: 16px;
        line-height: 26px;
        color: #3f9a55;
        cursor: pointer;
      }
      .addressWrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        .address {
          // max-width: 35ch;
          // width: 80%;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          background: rgba(63, 154, 85, 0.1);
          border-radius: 10px;
          padding: 10px 10px;
          color: $color-primary;
          font-weight: bold;
          font-size: 14px;
          letter-spacing: 0.5px;
          margin-bottom: 10px;
        }
      }
    }
  }
}
@media (max-width: 765px) {
  .accountDetail {
    .account {
      > p {
        max-width: 25ch;
      }

      .addressWrap {
        .address {
          max-width: 35ch;
          font-size: 14px;
        }
      }
    }
  }
}
.walletModalWrapper {
  :global(.ant-modal-content) {
    box-sizing: border-box;
    background: white;
    border: 1px;
    border-radius: 10px;
    padding: 0;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    font-family: 'Battambang', sans-serif;
  }
  :global(.ant-modal-body) {
    padding: 15px 20px;
  }
}
