@import '../../Variables.scss';
.popup {
  position: relative;
  width: 100%;
  .title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2%;

    p {
      margin: 2% 0;
      font-size: 18px;
      font-weight: bold;
    }

    img {
      cursor: pointer;
      width: 12px;
      height: 12px;
    }
  }

  .walletCard {
    width: 100%;
    background-color: white;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    justify-content: center;
    border: 1px rgba(#000000, 0.1) solid;
    overflow: hidden;
    p {
      color: $color-primary;
      font-size: 11px;
      color: #000000;
      opacity: 0.5;
      white-space: nowrap;
      margin: 10px 0 0 0;
    }
    img {
      width: 30px;
      height: 30px;
    }
    &:hover {
      background: rgba(176, 176, 176, 0.1);
      border: 1px rgba(#000000, 1) solid;
    }
  }

  .closeIcon {
    width: 16px;
    height: 16px;
    margin-left: 100%;
    padding-right: 50px;
    cursor: pointer;
  }
  .accountTitle {
    font-size: 21px;
    font-weight: 500;
    padding: 2%;
    p {
      margin-top: 0;
    }

    // margin: 0;
  }

  .disconnectButton {
    width: 115px;
    height: 35px;
    vertical-align: middle;
    line-height: 35px;
    background-color: lightgray;
    border-radius: 60px;
    margin: auto;
    cursor: pointer;
    p {
      width: 100%;
      text-align: center;
    }
  }
}

