@import '../../../Variables.scss';
.starterBlock {
  background-color: #fff;
  // border: 1px solid #dadee8;
  box-sizing: border-box;
  // border-radius: 6px;
  // padding: 20px;

  h3 {
    color: #12162d;
    font-weight: 700;
    font-size: 16px;
    line-height: 25px;
  }
  > p {
    font-weight: 400;
    font-size: 14px;
    line-height: 25px;
    color: #12162d;
    opacity: 0.5;
  }
  .starterItem {
    position: relative;
    overflow: hidden;
    width: 100%;
    border-radius: 50%;
    border-bottom-right-radius: 5px;

    cursor: pointer;
    border: 4px solid transparent;
    &:hover,
    &.selected {
      border: 4px solid $color-primary;
      background-color: $color-primary;
      .corner,
      .checkMark {
        display: block;
      }
    }
    img {
      width: 100%;
    }
    .corner {
      display: none;
      position: absolute;
      right: 0;
      bottom: 0;
      width: 0;
      height: 0;
      border-bottom: 25px solid $color-primary;
      border-left: 25px solid transparent;
    }
    .checkMark {
      display: none;
      position: absolute;
      right: 0;
      bottom: 0;
      color: white;
    }
  }
}
