.inviterInfoWrapper {
  position: relative;
  width: 100%;
  height: calc(179.6px - 24.8px);
  padding: 20px;
  background: #ffffff;
  border: 1px solid #dadee8;
  box-sizing: border-box;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  > div {
    color: rgba($color: #12162d, $alpha: 0.5);
    font-weight: 700;
    font-size: 16px;
    line-height: 16px;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: 8px 0;
    padding: 8px 0;
    > p {
      margin-bottom: 0;
      padding-top: 3px;
    }
    > span {
      display: block;
      float: right;
      font-size: 16px;
      line-height: 16px;
      text-align: right;
      color: #3f9a55;
    }
  }
  div:nth-child(2) {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    // border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding-top: 20px;
    margin-top: -10px;
  }
  .totalPoints {
    > img {
      display: inline-block;
      margin-right: 5px;
      margin-top: -3px;
    }
    > span {
      font-weight: 700;
      font-size: 16px;
      line-height: 16px;
      text-align: right;
      color: #000000;
    }
  }
}
