@import '../../Variables.scss';
.MainButton {
  position: relative;
  min-width: 100px;
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: 700;
  height: 40px;
  padding: 0 15px;
  background-color: $color-primary;
  box-shadow: 0 4px 10px rgba(63, 154, 85, 0.5);
  color: white;
  border-radius: 4px;
  cursor: pointer;
  user-select: none;
  white-space: nowrap;
  &.secondary {
    background-color: white;
    border: 1px solid $color-primary;
    color: $color-primary;
    box-shadow: none;
  }

  &.red {
    border: 2px solid $color-red;
    background-color: $color-red;
    box-shadow: 0 4px 10px #f10d0d8f;
    color: white;
  }

  &.disabled {
    opacity: 0.7;
    cursor: not-allowed;
  }
}
