.Signup {
  position: relative;
  .avatarWrap {
    width: 100%;
    margin-bottom: 10px;
    .avatarHeader {
      .avatar {
        width: 20%;
        cursor: pointer;
      }
      .arrow {
        margin-left: 15px;
        width: 20px;
        cursor: pointer;
      }
    }
    .collapseWrap {
      width: 100%;
      margin-top: 10px;
      max-height: 170px;
      transition: max-height 0.3s ease-in-out;
      overflow-y: scroll;
      overflow-x: hidden;
      -ms-overflow-style: none; /* IE and Edge */
      &::-webkit-scrollbar {
        display: none;
      }
      &.collapsed {
        max-height: 0;
        // overflow: hidden;
      }
    }
  }
  .formLabel {
    font-weight: bold;
    font-size: 14px;
    color: rgba(18, 22, 45, 0.5);
    margin-left: 5px;
    user-select: none;
    cursor: pointer;
  }
}
