.ReferralInfoWrapper {
  margin-top: 30px;
  > span {
    // display: block;
    font-weight: 700;
    font-size: 24px;
    line-height: 25px;
    color: #12162d;
  }
  .referralDetail {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    .inviterInfo,
    .invitationInfo {
      flex: 1 1;
    }
    .inviterInfo {
      align-self: flex-end;
    }
  }
}
@media screen and (max-width: 765px) {
  .ReferralInfoWrapper {
    > span {
      display: inline-block;
      margin: 10px 0;
    }
    .referralDetail {
      flex-direction: column;
    }
  }
}
