@import '../../../Variables.scss';
.ProjectItem {
  position: relative;
  width: 100%;
  padding: 10px;
}

.itemCard {
  position: relative;
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  // box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);
  border:1px solid #F0F0F0;
  border-radius: 4px;
  &:hover {
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
  }
}
.logo {
  width: 130px;
  height: 130px;
}
.textWrap {
  position: relative;
  width: 100%;
  text-align: left;
  h2 {
    font-weight: 900;
    font-size: 20px !important;
    color: $color-primary;
    margin: 10px 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: 25px;
    text-transform: capitalize;
  }
  p {
    font-size: 12px;
    line-height: 17px;
    //min-height: 100px;
    height: 120px;
    text-align: justify;
    overflow-y: scroll;
    -ms-overflow-style: none; /* IE and Edge */
    &::-webkit-scrollbar {
      display: none;
    }
  }
}

.statsWrap {
  position: relative;
  width: 100%;
  text-align: left;
  background-color: $color-dark-gray;
  border-radius: 4px;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 20px;
  .statsItem {
    flex: 1;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    &:not(:first-child) {
      //margin-top: 5px;
    }
    h2 {
      line-height: 25px;
    }
    .totalStats {
      display: flex;
      align-items: center;
      h2 {
        margin: 0;
        font-size: 18px;
        font-weight: 900;
        // line-height: 25px;
      }
      img {
        height: 16px;
        margin-right: 10px;
      }
    }
  }
  .recentStats {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-start;
    h2 {
      margin: 0;
      font-weight: bold;
      font-size: 18px;
      color: $color-primary;
    }
    p {
      margin: 0;
    }
  }
}

@media (max-width: 765px) {
  .logo {
    width: 100px;
    height: 100px;
  }
  .textWrap {
    h2 {
      font-size: 16px;
    }
    p {
      font-size: 12px;
      line-height: 17px;
    }
  }

  .statsWrap {
    position: relative;
    width: 100%;
    text-align: left;
    background-color: $color-dark-gray;
    border-radius: 4px;
    padding: 10px;
    .statsItem {
      width: 100%;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      &:not(:first-child) {
        margin-top: 5px;
      }
      h2 {
        line-height: 25px;
      }
      .totalStats {
        display: flex;
        align-items: center;
        h2 {
          margin: 0;
          font-size: 18px;
          font-weight: 900;
          // line-height: 25px;
        }
        img {
          width: 20px;
        }
      }
    }
    .recentStats {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: flex-start;
      h2 {
        margin: 0;
        font-weight: bold;
        font-size: 18px;
        color: $color-primary;
      }
      p {
        margin: 0;
      }
    }
  }
}
