.ChainButton {
  position: relative;
  height: 100%;
  width: 100%;
}
.chainIcon {
  border: 1px solid #3f9a55;
  border-radius: 4px;
  position: relative;
  // padding: 4px;
  padding: 5px 0;
  height: 100%;
  display: flex;
  justify-content: center;
  img {
    display: block;
    // width: 100%;
    height: 100%;
  }
  .chainName {
    display: none;
  }
  .caretDown {
    position: absolute;
    right: -1px;
    top: -5px;
  }
}
.title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2%;

  p {
    margin: 2% 0;
    font-size: 18px;
    font-weight: bold;
  }

  img {
    cursor: pointer;
    width: 12px;
    height: 12px;
  }
}
.walletModalWrapper {
  :global(.ant-modal-content) {
    box-sizing: border-box;
    background: white;
    border: 1px;
    border-radius: 10px;
    padding: 0;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    font-family: 'Battambang', sans-serif;
  }
  :global(.ant-modal-body) {
    padding: 15px 20px;
  }
}
@media (max-width: 765px) {
  .chainIcon {
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
    .caretDown {
      position: unset;
    }
    .chainName {
      display: block;
      margin-bottom: 0;
      font-size: 18px;
    }
    img {
      // width: 20px;
      height: 50%;
    }
  }
}
