@import '../../../Variables.scss';
.NavItem {
  position: relative;
  width: 100%;
  height: 100px;
  background-color: white;
  //border: 1px solid $color-border-gray;
  //border-radius: 8px 32px;
  border: 2px solid $color-border-green;
  display: flex;
  justify-content: space-between;
  text-align: left;
  padding: 10px 20px;
  //margin-bottom: 20px;
  box-sizing: border-box;
  user-select: none;
  overflow: hidden;
  cursor: pointer;

  .textWrap {
    width: 85%;
    h2 {
      font-weight: 900;
      font-size: 18px;
      line-height: 20px;
      color: #000000;
      opacity: 0.7;
      margin: 5px 0;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      text-transform: capitalize;
    }
    p {
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;
      color: #000000;
      opacity: 0.7;
      margin: 0;
      overflow: hidden;

      text-overflow: ellipsis;
    }
  }
  .stateWrap {
    font-size: 20px;
    font-weight: 900;
    //font-style: bold;
  }

  &.inprogress {
    .textWrap {
      h2 {
        opacity: 1;
      }
      p {
        opacity: 1;
      }
    }
  }

  &.completed {
    .textWrap {
      h2 {
        color: $color-primary;
        opacity: 1;
      }
      p {
        opacity: 1;
        color: $color-primary;
      }
    }
    .stateWrap {
      color: $color-primary;
    }
  }

  &.active {
    //border: 2px solid $color-primary;
    background-color: $color-border-green;
  }
  // &.finished {
  //   .textWrap {
  //     h2 {
  //       color: $color-primary;
  //       opacity: 1;
  //     }
  //     p {
  //       opacity: 1;
  //     }
  //   }
  //   .stateWrap {
  //     color: $color-primary;
  //   }
  // }
}
