@import '../../Variables.scss';
.Header {
  position: relative;
  width: 100%;
  height: 100px;
  background-color: white;
  overflow: hidden;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
}
.headerContainer {
  height: 100%;
  display: flex;
  align-items: center;
  // justify-content: space-between;
  padding: 0;

  .logo {
    width: 250px;
  }

  .menuWrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    flex-grow: 1;
    .navLinkWrap {
      .arrow {
        display: none;
      }
      height: 100%;
      display: flex;
      align-items: center;
      margin-left: 30px;
      .navLink {
        margin-right: 30px;
        //height: 100%;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        h2 {
          font-weight: 500;
          font-size: 18px;
          margin: 0;
          white-space: nowrap;
          color: $color-black-tab;
        }
        &.navLinkActive,
        &:hover {
          h2 {
            color: $color-primary;
          }
          border-bottom: 2px solid $color-primary;
          border-top: 2px solid transparent;
        }
      }
    }
    .buttonWrap {
      display: flex;
      align-items: center;
      column-gap: 15px;
      .connectButton {
        position: relative;
        background: #edf0ea;
        box-shadow: none;
        height: 40px;
        color: black;
        > img {
          width: 25px;
          margin-right: 5px;
        }
        .check {
          width: 25px;
          height: 25px;
          position: absolute;
          right: 0;
          bottom: 0;
        }
        &.green {
          background-color: $color-primary;
          color: white;
        }
      }
    }
  }
  .chainButton {
    // margin-left: 10px;
    height: 40px;
    width: 40px;
  }
  .countryIcon {
    margin-left: 15px;
    // padding: 0 10px;
    > img {
      height: 40px;
    }
  }

  .langButton {
    height: 40px;
    border: 1px solid $color-primary;
    box-sizing: border-box;
    filter: drop-shadow(0px 4px 10px rgba(63, 154, 85, 0.5));
    border-radius: 4px;
    line-height: 40px;
    width: 100px;
    text-align: center;
    padding: 0 10px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    color: $color-primary;
    font-weight: bold;
    margin-left: 20px;
    cursor: pointer;
    user-select: none;
  }

  .hamburger {
    display: none;
  }
}

.walletModalWrapper {
  :global(.ant-modal-content) {
    box-sizing: border-box;
    background: white;
    border: 1px;
    border-radius: 10px;
    padding: 0;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    font-family: 'Battambang', sans-serif;
  }
  :global(.ant-modal-body) {
    padding: 15px 20px;
  }
}

@media (max-width: 765px) {
  .Header {
    position: fixed;
    height: 60px;
    z-index: 2;
  }
  .headerContainer {
    width: 100%;
    padding: 0 20px;
    position: relative;
    justify-content: space-between;
    .burgerWrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      .hamburger {
        display: block;
        font-size: 20px;
        // flex-grow: 2;
        text-align: right;
        color: $color-primary;
      }
      .logo {
        width: auto;
        height: 20px;
      }
    }

    .menuWrap {
      flex-direction: column;
      justify-content: flex-start;
      position: fixed;
      top: 60px;
      left: 0;
      z-index: 10;
      height: 100%;
      width: 250px;
      background-color: white;
      border-radius: 0 0 5px 5px;
      flex-grow: 0;
      row-gap: 20px;
      // padding: 20px;

      .navLinkWrap {
        flex-direction: column;
        row-gap: 10px;
        // justify-content: space-between;
        margin: 0;
        // height: 30%;
        height: fit-content;
        width: 90%;
        .arrow {
          display: flex;
          color: $color-primary;
          font-size: 24px;
          // height: 33.3%;
          width: 100%;
          align-items: center;
          justify-content: center;
          &:hover {
            background: rgba($color: #3f9a55, $alpha: 0.1);
          }
        }
        .navLink {
          height: fit-content;
          box-sizing: border-box;
          display: flex;
          align-items: center;
          justify-content: center;
          margin: 0;
          width: 100%;
          // height: 33.3%;
          h2 {
            text-align: center;
            // font-weight: bold;
            font-size: 22px;
            // margin: 0;
            // width: 100%;
          }
          &.navLinkActive,
          &:hover {
            background: rgba($color: #3f9a55, $alpha: 0.1);
            border-bottom: none;
            border-radius: 10px;
            //border-top: 5px solid transparent;
          }
        }
      }
      .buttonWrap {
        flex-direction: column;
        margin-top: 20px;
        row-gap: 10px;
        width: 90%;
        // height: 20%;
        .connectButton {
          // height: 30%;
          width: 100%;
          // font-size: 26px;
          // font-weight: 400;
          // img {
          //   width: 20px;
          // }
          // .check {
          //   width: 21px;
          //   height: 22px;
          // }
        }
      }
      .chainButton {
        margin-left: 0;
        // height: 30%;
        width: 100%;
      }
    }
    .countryIcon {
      > img {
        height: 30px;
      }
    }
  }
}
