@import '../../../Variables.scss';
.DesignForm {
  position: relative;
  width: 100%;
  //display: flex;
  //align-items: flex-start;
  //justify-content: space-between;
}

.formNavWrap {
  //width: 30%;
  display: flex;
  justify-content: space-between;
  border: 2px solid $color-border-green;
  margin-bottom: 20px;
  //gap: 20px;
}

.formInputWrap {
  //width: 65%;
  background-color: $color-gray-background;
  //border-radius: 8px;
  //border: 1px solid $color-border-gray;
  padding: 40px 0;
}
.formContainer {
  width: 90%;
  margin: auto;
}
.buttonWrap {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}
.formButton {
  width: 100px;
  &.hide {
    visibility: hidden;
  }
}
.submitButton {
  width: 150px;
  margin: 20px auto;
  &.inactive {
    opacity: 0.5;
  }
}
.confirmModal {
  :global(.ant-modal-content) {
    box-sizing: border-box;
    background: white;
    border: 1px;
    border-radius: 10px;
    padding: 0;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    font-family: 'Battambang', sans-serif;
  }
  :global(.ant-modal-body) {
    padding: 15px 20px;
  }
  h2 {
    font-size: 18px;
    font-weight: bold;
  }
}

@media (max-width: 765px) {
  .DesignForm {
  }
  .formNavWrap {
    width: 100%;
    flex-wrap: wrap;
  }
  .formInputWrap {
    width: 100%;
  }
}
