@import '../../../../Variables.scss';
.BindModal {
  .notes {
    margin-bottom: 0;
  }

  .walletAddressWrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0;

    .address {
      // width: calc(100% - 60px);
      overflow-x: hide;
      white-space: nowrap;
      background: rgba(63, 154, 85, 0.1);
      border-radius: 10px;
      padding: 10px 10px;
      color: $color-primary;
      font-weight: bold;
      font-size: 12px;
      letter-spacing: 0.5px;
    }
    .copyButton {
      color: $color-primary;
      font-weight: bold;
      cursor: pointer;
    }
  }
  .bindButton {
    margin-left: auto;
    margin-right: auto;
  }
}
