@import '../../../Variables.scss';

.DetailForm {
  position: relative;
  width: 100%;
  :global(.ant-form-item-label) {
    label {
      font-weight: bold;
      font-size: 14px;
      color: $color-primary;
    }
  }
  :global(.ant-form) {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    column-gap: 20px;
  }
  :global(.ant-form-item) {
    width: calc(50% - 10px);
  }
  .antFormText {
    font-weight: 700;
    color: $color-text-gray;
    margin-left: 5px;
  }
  text-align: left;

  :global(.ant-switch-checked) {
    background-color: $color-primary;
  }
  .addressRatio {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    :global(.ant-form-item) {
      &:nth-child(1) {
        width: 65%;
      }
      &:nth-child(2) {
        flex: 1;
        margin-left: 5px;
        margin-right: 5px;
      }
    }
  }
  .addButton {
    float: right;
    color: $color-primary;
    cursor: pointer;
  }

  @media (max-width: 765px) {
    :global(.ant-form-item) {
      width: calc(100%);
    }
  }
}

.titleWrap {
  h2 {
    font-weight: bold;
    font-size: 24px;
    line-height: 25px;
    color: #000000;
    text-align: left;
  }
  p {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 30px;
    color: #000000;
  }
}

.suffix {
  color: $color-primary;
  margin: 0;
}
