@import '../../Variables.scss';

.AuthForm {
  position: relative;
  width: 100%;
  .titleWrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
    h1 {
      font-weight: 700;
      font-size: 20px;
      text-transform: uppercase;
      margin-bottom: 0;
    }
    img {
      cursor: pointer;
      width: 12px;
      height: 12px;
    }
  }

  :global(.ant-form-item-label) {
    label {
      font-weight: bold;
      font-size: 14px;
      color: $color-text-gray;
    }
    padding: 0 0 5px !important;
  }
  :global(.ant-input) {
    background: rgba(71, 106, 48, 0.1) !important;
    border-radius: 4px;
    // border: none;
    color: $color-primary;
    opacity: 1;
    &:hover {
      border-color: $color-primary;
      box-shadow: 0 0 0 2px rgb($color-primary, 0.2);
    }
    &:focus {
      border-color: $color-primary;
      box-shadow: 0 0 0 2px rgb($color-primary, 0.2);
    }
    &::placeholder {
      /* Chrome, Firefox, Opera, Safari 10.1+ */
      color: $color-primary;
      opacity: 0.7; /* Firefox */
    }

    &:-ms-input-placeholder {
      /* Internet Explorer 10-11 */
      color: $color-primary;
      opacity: 0.7;
    }

    &::-ms-input-placeholder {
      /* Microsoft Edge */
      color: $color-primary;
      opacity: 0.7;
    }
  }
  :global(.ant-checkbox-checked .ant-checkbox-inner) {
    background-color: $color-primary;
    border-color: $color-primary;
  }
  .link {
    color: $color-primary;
    font-weight: 700;
    font-size: 14px;
    cursor: pointer;
    user-select: none;
  }
  .sendVerify {
    margin-left: 10px;
  }
  .forget {
    float: right;
  }
  .actionWrap {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .actionButton {
      width: 120px;
    }
    p {
      font-weight: 700;
      font-size: 14px;
      color: black;
      margin-bottom: 0;
    }
  }
}
