@import '../../../Variables.scss';

.ProfileOverview {
  position: relative;
  width: 100%;
  .titleWrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
    h1 {
      font-weight: 700;
      font-size: 20px;
      text-transform: uppercase;
      margin-bottom: 0;
    }
    img {
      cursor: pointer;
      width: 12px;
      height: 12px;
    }
  }
  .contentWrap {
    position: relative;
    width: 100%;
    border: 1px solid #f2f2f2;
    margin: 20px 0;
    .header {
      position: relative;
      width: 100%;
      height: 100px;
      background: radial-gradient(133.33% 277.33% at 3.25% -21.67%, #ffdc30 0%, #26b248 100%);
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: right bottom;
      }
    }
    .infoWrap {
      position: relative;
      padding: 50px 10px 10px 10px;
      .avatar {
        position: absolute;
        left: 50%;
        top: 0;
        transform: translate(-50%, -50%);
        width: 80px;
        border: 1px solid $color-primary;
        border-radius: 50%;
      }
      .nickname {
        text-align: center;
        font-weight: 400;
        font-size: 16px;
        line-height: 16px;
      }
      .itemValue {
        display: flex;
        align-items: center;
        justify-content: center;
        span {
          color: #12162d;
          font-weight: bold;
          font-size: 16px;
          padding-top: 3px;
        }
        img {
          width: 20px;
          margin-right: 5px;
        }
      }
      .profileLink {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        font-size: 14px;
        color: $color-primary;
        font-weight: 600;
        margin-top: 20px;
        img {
          width: 20px;
          margin-left: 5px;
        }
      }
    }
  }
  .logoutButton {
    width: 100%;
  }
}
