@import '../../Variables.scss';

.ProjectDesign {
  position: relative;
  width: 100%;
  overflow-x: hidden;
  background-color: #fff;
  min-height: 100vh;
}

.banner {
  width: 100%;
  height: 100%;
  //background-blend-mode: hard-light;
  background-position: right top;
  background-repeat: no-repeat;
  background-size: cover;
  // background-color: $color-primary;
  .bannerContainer {
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: space-between;
    h2 {
      font-weight: 900;
      font-size: 30px;
      color: $color-primary;
      margin: 0;
    }
    .instructionWrap {
      display: flex;
      align-items: center;
      a {
        display: flex;
        align-items: center;
        margin-right: 30px;
        gap: 10px;
        font-weight: 700;
        font-size: 20px;
        text-decoration: none;
        img {
          width: 25px;
          //margin-right: 10px;
        }
      }
    }
  }
}
@media (max-width: 765px) {
  .innerWrapper {
    background: rgba(255, 255, 255, 0.7);
  }
  .banner {
    .bannerContainer {
      flex-direction: column;
      .instructionWrap {
        flex-direction: column;
        align-items: flex-start;
        a {
          margin-top: 10px;
          margin-right: 0;
        }
      }
    }
  }
}
